import React from 'react';
import { BiPluginConfig } from '../../types';
import AlignmentMenu from './components/sidebarItems';

function sidebar(config: BiPluginConfig) {
  const { alignment = {} } = config;
  const { locale = {} } = alignment as Record<string, any>;
  return {
    items: {
      default: (props) => (
        <AlignmentMenu {...props} locale={locale} />
      ),
    },
    config: alignment,
    locale,
  };
}

export default sidebar;
